import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { DatosPage } from '../pages/datos/datos';
import { SilabaPage } from '../pages/silaba/silaba';
import { FacebookPage } from '../pages/facebook/facebook';
import { TablasPage } from '../pages/tablas/tablas';
import { AsistenciaPage } from '../pages/asistencia/asistencia';
import { AgendaPage } from '../pages/agenda/agenda';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TextToSpeech } from '@ionic-native/text-to-speech';
import { IonicStorageModule } from '@ionic/storage';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    DatosPage,
    SilabaPage,
    FacebookPage,
    TablasPage,
    AsistenciaPage,
    AgendaPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/datos/datos.module#DatosPageModule', name: 'DatosPage', segment: 'datos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/silaba/silaba.module#SilabaPageModule', name: 'SilabaPage', segment: 'silaba', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/facebook/facebook.module#FacebookPageModule', name: 'FacebookPage', segment: 'facebook', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tablas/tablas.module#TablasPageModule', name: 'TablasPage', segment: 'tablas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/asistencia/asistencia.module#AsistenciaPageModule', name: 'AsistenciaPage', segment: 'asistencia', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/agenda/agenda.module#AgendaPageModule', name: 'AgendaPage', segment: 'agenda', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    DatosPage,
    SilabaPage,
    FacebookPage,
    TablasPage,
    AsistenciaPage,
    AgendaPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    TextToSpeech,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



